				}
				_self.goods.push(good);
			}
		},
		onPageScroll(e){
			this.ceiling = e.scrollTop >= this.scrollTop ? true : false;
		},
		mounted() {
		},
		watch: {
			seckillTabs() {
				this.seckillTabs.forEach(tab => {
					tab.data = moment(tab.data).format("HH:mm");
				})
			},
		},
		methods: {
			goGoodDetail(id){
				uni.navigateTo({
					url:`./good?id=${id}`
				})
			}
		}
	}
</script>

